import React from 'react';
import {Link} from 'react-router-dom';
import './main.css';

    function main(){

        return(
                <div className="main">
                        <Link to="/main/gestion" className="action">
                            <span>Gérer vos classes</span>
                        </Link>
                        
                        <Link to="#" className="action classement">
                            <span>Classement des étudiants</span>
                        </Link>

                        <Link 
                              to="/main/ajouterUneClasse"
                              className="action"
                        >
                           <span>Ajouter une classe</span>
                        </Link>
                        
                        <Link to="#" className="action autre">
                                <span>Autre</span>
                        </Link>
                </div>
        );
    }



export default main;