import React , {useState, useReducer} from 'react';
import { Redirect } from 'react-router-dom';
import "./main.css";


    const setErrors = (errors,action) => {
            const {type} = action;
        switch(type)
        {
                case "CLASSREF_ERR":
                        return( {...errors, classRef : true} )
                
                case "CLASSREF_NOERR":
                        return( {...errors, classRef : false} )
                

                case "FILIERE_ERR":
                    return( {...errors, filiere : true} )
                    
                case "FILIERE_NOERR":
                    return( {...errors, filiere : false} )

                case "SEMESTRE_ERR":
                        return( {...errors, semestre : true} )
                        
                case "SEMESTRE_NOERR":
                        return( {...errors, semestre : false} )
                        
                default:
                    console.log("Invalid option");
        }
    }

const AjouterClasse = () =>{

    const [newClassData, setNewClassData] = useState({
            classRef: "",
            filiere : "",
            semestre : ""
    });

    const [errors, dispatch] =useReducer(setErrors,{
        classRef: false,
        filiere : false,
        semestre : false
    });

    const [toRedirect, setToRedirect]=useState(false);

    const getNewClassData = (e) =>{
            const {name,value} = e.target;

            setNewClassData({
                ...newClassData,
                [name]: value
            })
    } 

            const verifie = (data)=>  {

                    if(data.classRef === "" )
                    {
                            dispatch({type: "CLASSREF_ERR"});
                        return false;
                    }
                    else
                    {
                        dispatch({type: "CLASSREF_NOERR"});
                    }
                
                    if(data.filiere ==="" )
                    {
                        dispatch({type: "FILIERE_ERR"});
                        return false;
                    }else{
                        dispatch({type: "FILIERE_NOERR"});
                    }
                    
                    if(data.semestre ==="" )
                    {
                        dispatch({type: "SEMESTRE_ERR"});
                        return false;
                    }else
                    {
                        dispatch({type: "SEMESTRE_NOERR"});
                    }
                    
                    if(data.classRef.length<5 || !isNaN(data.classRef.charAt(0)))
                    {
                        dispatch({type: "CLASSREF_ERR"});
                        return false;
                    }else
                    {
                        dispatch({type: "CLASSREF_NOERR"});
                    }

                    return true;
            }

    const handleSubmit = (e) =>{
        e.preventDefault();
            if(verifie(newClassData))
                {
                    alert("Add this data to database now ");
                    setToRedirect(true);
                }
            else
                alert("Show error")
    }


    return(
            <div className="newClassRoom">
                { toRedirect===false && <form>
                    <label>
                        Référence du classe (id):
                    </label>
                    <input
                            type="text"
                            name="classRef"
                            value={newClassData.classRef}
                            onChange={event => getNewClassData(event)}
                    />
                    { errors.classRef && <span className="Errors">
                         Veillez ajouter use référence pour la classe 
                                <br/>-commence par un lettre
                                <br/>-au moins 4 caractères
                     </span>}     

                    <label>
                        Filière :
                    </label>

                    <select 
                      name="filiere"
                      value={newClassData.filiere}
                      onChange={event => getNewClassData(event)}
                    >
                        <option
                            value=""
                        >
                            Choisir la filière
                        </option>
                        <option 
                            value="SMI"
                        >
                            SMI
                        </option>
                        <option
                        value="SMA"
                        >
                            SMA
                        </option>
                        <option
                        value="SMP"
                        >
                            SMP
                        </option>
                        <option
                        value="SMC"
                        >
                            SMC
                        </option>
                        <option
                        value="SU"
                        >
                            SU
                        </option>
                        <option
                        value="SV"
                        >
                            SV
                        </option>
                    </select>
                    { errors.filiere && <span className="Errors"> Veillez choisir la filiere </span>}     

                    <label>
                        Semestre :
                    </label>
                    <select 
                    name="semestre"
                    value={newClassData.semestre}
                    onChange={event => getNewClassData(event)}
                    >
                        <option
                            value=""
                        >
                            Choisir la semestre
                        </option>
                        <option
                            value="S1"
                        >
                            S1
                        </option>
                        <option
                        value="S2"
                        >
                            S2
                        </option>
                        <option
                        value="S3"
                        >
                            S3
                        </option>
                        <option
                        value="S4"
                        >
                            S4
                        </option>
                        <option
                        value="S5"
                        >
                            S5
                        </option>
                        <option
                        value="S6"
                        >
                            S6
                        </option>
                    </select>
                    { errors.semestre && <span className="Errors"> Veillez choisir la semestre </span>}     

                    <button 
                        type="submit"
                        className="btn"
                        onClick={event => handleSubmit(event)}
                    >
                        Ajouter
                    </button>
                </form>}

                { toRedirect===true && <Redirect to="/main" />}                
            </div>
    );
}

export default AjouterClasse;