import React, { useState, useContext, useRef } from "react";
import { Redirect } from "react-router-dom";
import {LogUpForm} from "./LogUpForm";
import "./loginup.css";
import { userContext } from '../App';



function LogOut() {

    const [user] = useContext(userContext);
    const [afficherPass, setAfficherPass] = useState(false);
    const [finished, setFinished] = useState(false);
    const [newUserData, setNewUserData] = useState({
        email: "",
        password: "",
        secondPass: ""
    });
    

    const passwordError = useRef(null);
    const emailError = useRef(null);
    const confirmError = useRef(null);


    const collectNewUserData = (e) => {
        const { name, value } = e.target;

        setNewUserData({
            ...newUserData,
            [name]: value
        })
    }

    const verifieData = (email, password, secondPass) => {

        if (email === "" || email.indexOf('@') < 0) {
            emailError.current.style.display = "block";
            return false;
        } else {
                   emailError.current.style.display = "none";
        }

        if (password === "" || password.length < 8) {
            passwordError.current.style.display = "block";
            return false;
        } else {
            passwordError.current.style.display = "none";
        }
        
        if(secondPass !== password)
        {
                confirmError.current.style.display = "block";
                return false;
        }
        else{
                confirmError.current.style.display = "none";
        }

        return true;
    }

    
    
    const handleSubmit = (e) => {
        e.preventDefault();
        if (verifieData(newUserData.email, newUserData.password,newUserData.secondPass))
            setFinished(true);
        else
            alert('Corriger les erreurs');
    }
    return (
        <div className="Loginup LogUp">

            {   user === null 
                    && 
                <LogUpForm
                        afficherPass={afficherPass}
                        setAfficherPass={setAfficherPass}
                        handleSubmit={handleSubmit}
                        collectNewUserData={collectNewUserData}
                        newUserData={newUserData}
                        passwordError={passwordError}
                        confirmError={confirmError}
                        emailError={emailError}
                />
                
            }

            { finished &&

                <Redirect to="logIn" />

            }

        </div>

    );
}

export default LogOut;