import React, { useEffect, useState } from  "react";
import {Link,useParams} from "react-router-dom";
import Classes from "../databaseSimulation/Classes";
import AddStudent from "./AddStudent";
import "./main.css";
    function InsideClass()
    {
        const {ClassRef}=useParams();
        const [classe,setClasse]=useState({
            userId:0,
            classId:0,
            classDetails:{
                classRef: "",
                filiere: "",
                semestre: "",
                modules: []
            }
        });
        const [addStudent,setAddStudent] = useState(false);

        useEffect(()=>{
            var uneClasse;
            for(var i in Classes)
            {
                if(Classes[i].classDetails.classRef===ClassRef)
                    {
                        uneClasse=Classes[i];
                        break;
                    }
            }
            setClasse(classe => {
                return uneClasse
            });
        },[ClassRef]);
        
        return(
                <>  
                    <div className="insideClass"> 
                       <h1>
                        Gestion de la classe: {classe.classDetails.classRef}
                       </h1> 
                        <ul className="control">
                            <li>
                            <button 
                            onClick={()=>setAddStudent(addStudent => {
                                return !addStudent;
                            })}
                            className="btn">
                                Ajouter un étudiant
                            </button>
                            </li>

                            <li>
                                <button className="btn">
                                 <Link to="/main/gestion">
                                      Retour au menu de gestion des classes
                                 </Link> 
                                </button>
                            </li>
                        </ul>
                     <div>
                           <table border="0" cellSpacing="0">
                                <thead>
                                    <tr>
                                        
                                        <th>
                                            Nom
                                        </th>
                                        <th>
                                            Prénom
                                        </th>
                                        <th>
                                            moyen
                                        </th>
                                        <th hidden>

                                        </th>
                                    </tr>
                                </thead>
                                
                                <tbody>
                                        <tr>
                                            <td>
                                                test
                                            </td>
                                            <td>
                                                test
                                            </td>
                                            <td>
                                                00,00
                                            </td>
                                            <td>
                                                details
                                            </td>
                                        </tr>
                                </tbody>
                        </table>
                    </div>


                    { addStudent===true && <AddStudent 
                                                filiere="SMI" 
                                                semestre="S5" 
                                                setAddStudent={setAddStudent} 
                                            />}

                    </div>
                </>
        );
    }

   




export default InsideClass;