import React, {createContext, useState, useEffect} from "react";
import {BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import './App.css';
import LogIn from './logInUp/LogIn';
import LogUp from './logInUp/LogUp';
import Header from './headerFooter/Header';
import Footer from './headerFooter/Footer';
import Home from './homeError/Home';
import Error from './homeError/Error';
import Main from './mainFunctionality/Main';
import AjouterClasse from './mainFunctionality/AjouterClasse';
import Gestion from "./mainFunctionality/Gestion";
import InsideClass from "./mainFunctionality/InsideClass";
import Profil from './profil/Profil';
import users from './databaseSimulation/users';


  export const userContext = createContext();

    const {Provider} = userContext;

function App() {
  
    const [user,setUser]=useState( JSON.parse(localStorage.getItem('user')) || null);
    console.log("app",user)
  return (
    <Provider  value={[user,setUser]}>
    <Router>
                    <div className="App">
                <Header/>
                <Switch>
                      <Route path="/" exact>
                              <Home/>
                      </Route>
                      <Route path="/LogIn">
                              <LogIn />
                        </Route>
                      <Route path="/LogUp" >
                              <LogUp />
                        </Route>
                      <Route path="/main" exact>
                                <Main />
                        </Route>
                      <Route path="/main/ajouterUneClasse">
                                <AjouterClasse />
                      </Route>
                      <Route path="/main/gestion" exact>
                                <Gestion />
                      </Route>
                      <Route path="/main/gestion/:ClassRef"  children={<InsideClass />} />
                      <Route path="/Profile/:userId"  children={<Profil />} />
                      <Route component={Error} />
                </Switch>
                <Footer />
              </div>
    </Router>
    </Provider>
  );
}

export default App;
