import React, {useState, useContext, useEffect} from "react";
import {Redirect} from 'react-router-dom';
import "./loginup.css";
import {Form} from './LogInForm';
import {userContext} from '../App';
// Simulating auth from an actual database
import users from '../databaseSimulation/users';
import Main from '../mainFunctionality/Main';



    function LogIn(){
            console.log(users);
            const [user,setUser]= useContext(userContext);
            const [afficherPass,setAfficherPass]=useState(false);
            const [valid,setValid]=useState(true);
            const [errors,setErrors]=useState({
                        email: false,
                        password: false
            });     

            const [ userData, setUserData]= useState({
                email: "",
                password: ""
            });

             const verifieData = (email,password) => {
                    console.log(email," ",password," index @",email.indexOf('@'));

                    if(email==="" || email.indexOf('@')<0)
                    {
                        setErrors(errors=>{

                            return {
                                email: true,
                                password: errors.password
                            }
                        });
                        return false;
                    }else
                    {
                        setErrors(errors=>{

                            return {
                                email: false,
                                password: errors.password
                            }
                        });
                    }

                    if(password==="" || password.length<8)
                    {
                        setErrors(errors=>{

                            return {
                                email: errors.email,
                                password: true
                            }
                        });
                        return false;
                    }else
                    {
                        setErrors(errors=>{

                            return {
                                email: errors.email,
                                password: false
                            }
                        });
                    }

                return true;
            }

            const isSignedUp = (email, password) =>{

                    
                    for(var i in users){
                        console.log("email:",email," ",password);
                        console.log(users[i]);
                        if(email == users[i].email && password == users[i].password)
                            {
                                setValid(true);
                                return true;
                            }
                    }
                    setValid(false);
                    return false;
            }

            const collectUser = (e) => {

                const {name,value} = e.target;
                
                    setUserData({ ...userData,
                        [name]: value
                    })
              

            }

            const handleSubmit = (e) => {
                    e.preventDefault();
                    if(verifieData(userData.email, userData.password))
                    {
                        if(isSignedUp(userData.email, userData.password))
                            {
                                setUser(userData);
                            }
                            
                    }             
            }

            return( <>
                {user === null && <Form userData={userData} collectUser={collectUser} handleSubmit={handleSubmit} afficherPass={afficherPass} setAfficherPass={setAfficherPass} errors={errors} valid={valid}/> }
                {useStorage(JSON.stringify(userData))}
                { user !== null && <Redirect to="/main" />  }
                </>
            )
    }


    const useStorage = (user)=>{
        useEffect(()=>{
                localStorage.setItem('user',user);
        })
    }



export default LogIn;