import React, {useState, useEffect} from 'react';
import { useParams } from 'react-router-dom';
import users from '../databaseSimulation/users';


function Profil(props) {
    
    const {userId}=useParams();
    
    const [userData,setUserData] = useState({});
        
    useEffect(
        ()=>{ 
               for(var elm in users)
               {
                    if( users[elm].id===parseInt(userId))
                    {
                       break;
                    }
               }
               setUserData(
                        (u)=>{
                           return users[elm];
                            }
               );
        },
        [userId]
    );

    const style={
        backgroundColor: userData.profile,
        color: userData.profile
    }

    

    return (
        <>
            <div className="profil">
                    <span
                        style={style}
                        className="Avatar">
                        a b c
                    </span>
                    <h2 className="email">
                        {userData.email}
                    </h2>
                    <h3>mes Classes:</h3>
                    <ul>
                        
                    </ul>
            </div>
        </>
    )
}




export default Profil;