import React, {useState, useContext,useEffect} from "react";
import {Link} from "react-router-dom";
import { userContext } from '../../components/App';
import users from '../databaseSimulation/users';
import "./isLogged.css";


    function IsLogged(){
        const [user] = useContext(userContext);   

        
            return( <div className='logs'>
               
                { user!==null ? <Logged /> : <Notlogged /> }
                
             </div>
             );}

    const Notlogged = () => {
    return (
    <div>
            <h3>
                Vous n'êtes pas connecté
            </h3>
     </div>);
    }

    const Logged = () => {
        const [user] = useContext(userContext);   
        const [userData,setUserData] = useState({});
        
        useEffect(
            ()=>{ 
                   for(var elm in users)
                   {
                        if( users[elm].email ===user.email )
                        {
                            setUserData(
                                    (u)=>{
                                        return users[elm];
                                    }
                            );
                        }
                   }
            },
            []
        );
        
        const style={
            backgroundColor : userData.profile,
            color: userData.profile
        };

        return(
    <div>
            <Link  to={`/Profile/${userData.id}`}>
                <span 
                    className="Avatar"
                    style={style}>
                         a b c
                </span>
                </Link>
                <span className="email">
                   {user.email}
                </span>
    </div>
        );
}


export default IsLogged;