export const LogUpForm = ({ afficherPass, setAfficherPass, handleSubmit, collectNewUserData, newUserData, passwordError, confirmError, emailError }) => {
        
    return (
        <form method="post">
            <fieldset>
                <legend>
                    S'inscrire :
                </legend>

                <label>
                    Email :
                </label>
                
                <input 
                    type="email"
                    name="email"
                    value={newUserData.email}
                    onChange={(event) => { collectNewUserData(event) }}
                />

                <span 
                    className="Errors" 
                    ref={emailError} 
                    style={{ display: "none" }}
                >
                     Veillez corriger l'email  
                </span>
                
                <div className="passContainer">
                    <label>
                        Mot de pass :
                    </label>
                    
                    <input 
                        type={afficherPass ? "text" : "password"}
                        value={newUserData.password}
                        name="password"
                        onChange={(event) => { collectNewUserData(event) }}
                    />

                    <button
                        type="button"
                        className="btn"
                        onClick={() => setAfficherPass(afficherPass => !afficherPass)}
                    >
                        {afficherPass ? "Cacher" : "Afficher"}
                    </button>
                    
                    <span 
                        className="Errors" 
                        ref={passwordError} 
                        style={{ display: "none" }}
                    > 
                        Le mot de passe est très court 
                    </span>

                </div>

                <div className="passContainer">

                    <label>
                        Confirmer le mot de pass :
                    </label>

                    <input 
                            type={afficherPass ? "text" : "password"}
                            value={newUserData.secondPass}
                            name="secondPass"
                            onChange={(event) => { collectNewUserData(event) }}
                    />

                    <button
                            type="button"
                            className="btn" onClick={() => setAfficherPass(afficherPass => !afficherPass)}
                    >
                            {afficherPass ? "Cacher" : "Afficher"}
                    </button>

                    <span 
                            className="Errors" 
                            ref={confirmError} 
                            style={{ display: "none" }}
                    > 
                    Les mots de passes ne sont pas les mêmes
                    </span>

                </div>

            </fieldset>

            <button 
                    className="btn" 
                    type="submit" 
                    onClick={(event) => handleSubmit(event)}
            >
                Envoyer
            </button>
    
    </form>
    );
}