import React from "react";
import { Link, Route } from "react-router-dom";
import Classes from "../databaseSimulation/Classes";
import "./main.css"

function Gestion(props) {
    
        

        const classes=Classes.map(classe=>{

            return <Classe 
                        key={classe.classId}  
                        classDetails={classe.classDetails} 
                        NomDeClass={classe.classId%2 ? "styleImpair" : "stylePair" }
                    />
        })

    return (
        <>
            <div className="gestion">
                <table cellSpacing="0" >

                    <thead>
                    <tr>
                            <td>
                                Référence de la classe
                            </td>
                    
                            <td>
                                Filière
                            </td>
                    
                            <td>
                                Semestre
                            </td>
                            <td >

                            </td>
                
                    </tr>

                    </thead>
                        
                    <tbody>
            
                    {classes}

                    </tbody> 
                </table>
                        
                </div>  
        </>
    )
}


function Classe(props)
{
    return(
            <tr className={props.NomDeClass}>
                
                <td className="classRef">
                    {props.classDetails.classRef}
                </td>

                <td className="filiere">
                    {props.classDetails.filiere}
                </td>

                <td className="semestre">
                    {props.classDetails.semestre}
                </td>

                <td className="goToClass">
                    <Link to={`/main/gestion/${props.classDetails.classRef}`}>
                        Editer cette classe
                    </Link>
                </td>

                </tr>
    )
}


export default Gestion;
