import React, {useContext} from "react";
import {Link} from 'react-router-dom';
import { userContext } from "../App";
import './homeError.css';



function Home(){
    const [user] = useContext(userContext);

        return( 
                <div className="homeError home">
                       <div>
                         <h1>
                            Bienvenue chez UniParcour
                        </h1>
                        <p>
                            Une méthode moderne et efficace pour suivre
                            le parcour et les notes des étudiants de votre établissment. 
                        </p>
                { user === null &&
                    <>
                  <Link to="LogIn" >
                            se connecter 
                        </Link>
                        <p>ou</p>
                        <Link to="LogUp">
                            s'inscrire
                        </Link> 
                    </>    
                }

                {
                    user !== null && 
                    <>  <p>Vous êtes déjà connecté</p>
                        <Link to="main" >
                            Aller à votre page. 
                        </Link>  
                    </>
                }
                </div>
                </div>
        );
    }


export default Home;