import React, {useState,useEffect} from "react";
import "./main.css";


const AddStudent = ({filiere, semestre,setAddStudent}) => {


    const [student,setStudent]=useState({
        Nom: "",
        Prenom: "",
        Notes:[]
    });

    const [modules,setModules]=useState([]);
    useEffect(()=>{

            switch(filiere)
            {
                case "SMI":
                    switch(semestre)
                     {
                         case "S5":
                                setModules(["BD1","OR","Rx","JAVA","C++","COM"]);
                            break;
                        default: 
                            console.log("ERROR");
                     }
                break;

                default: 
                console.log("ERROR");


            }


    },[filiere,semestre]);


    const handleSubmit = (event) =>{
        event.preventDefault();
        setAddStudent(addStudent=> {
            return !addStudent;
        } )
    } 


    const mods=modules.map((module)=>{

        return <li>
                    <label>
                        {module}
                    </label>
                    <input type="text" name={module} /> 
                </li>
    })

return(
            <div className="addstudent">
                <form>
                    <fieldset>
                    
                    <label>
                        Nom
                    </label>
                    <input 
                        type="text"
                        name="nom"
                    />

                    <label>
                        Prénom
                    </label>
                    <input 
                        type="text"
                        name="prenom"
                    />
                            <label>
                                Notes
                            </label>


                            <ul className="notes">
                                {mods}
                            </ul>


                    </fieldset>

                    <button 
                        className="btn ajtEtd"
                        onClick={event=>{ handleSubmit(event) }}
                        >
                            Ajouter</button>
                </form>
            </div>
)
}



export default AddStudent;