import React from 'react';

export const Form = ({ userData, collectUser, handleSubmit , afficherPass, setAfficherPass,errors, valid}) => {

    return(
        <div className="Loginup LogIn">
               <form method="post">
                       <fieldset>
                               <legend>
                                   Se connecter :
                               </legend>
                               <label>
                                   Email :
                               </label>
                               <input 
                               type="email" 
                               value={userData.email}  
                               name="email" 
                               placeholder="lach@med.com ou lach@mad.com"
                               onChange={(event) => collectUser(event)} 
                               />
                          { errors.email && <span className="Errors"> Corriger l'email </span>}     
                               <label>
                                   Mot de pass :
                               </label>
                               <div className="passContainer">
                               <input 
                               type={afficherPass ? "text" : "password"} 
                               name="password" 
                               placeholder="123456789 ou 12345678910"
                               value={userData.password} 
                               onChange={(event) => collectUser(event)} 
                               />    
                               <button
                                type="button"
                                className="btn"
                                onClick={ ()=> setAfficherPass(afficherPass => !afficherPass) }>
                                     
                                     { afficherPass ? "Cacher" : "Afficher" }

                               </button> 
                               
                               { errors.password && <span className="Errors">Mot de passe très courte </span>} 
                               </div>
                               
                       </fieldset>
                       <button type="submit" className="btn" onClick={(event) => handleSubmit(event)}>Connexion</button>
               </form>
        {     valid ||   <span className="Errors"> Vous n'êtes pas inscrits ! </span>}
       </div>
   )
}